.landingPage{
    width:900px;
    height: 100%;
    margin: auto;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-evenly;
   
}

.difficultyCard{
   
    margin:50px 100px;
    width: 250px;
    height: 300px;
    border-radius: 12px;
    cursor: pointer;
    box-shadow: 5px 5px 2.5px grey ;
    background-color: black;
    transition: scale 0.5s,transform 0.5s,box-shadow 0.5s;
}
.difficultyCard:hover{
    transform: scale(1.08);
    box-shadow:10px 10px 5px grey ;
}

.difficultyCard img{
    width: 100%;
    height: 82%;
    border-radius: 12px;
    object-fit: cover;
}
.difficultyCard>p{
   text-align: center;
    color: gold;
    font-size: 20px;
    margin: 10px auto;
    
}


@media only screen and (max-width: 486px) {

    .landingPage{
        width: 100%;
        
        flex-direction: column;
    }
    /* .difficultyCard{
        width: 99%;
        margin: 1%;
        
    } */
}